* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}
form {
  display: flex;
  margin: 1rem;
}
.btn {
  display: inline-block;
  color: #fff;
  background: #555555;
  cursor: pointer;
  padding: 7px 20px;
  border: none;
}

.btn:hover {
  background: #666;
}

.container {
  padding: 1rem 2rem;
}
